<script>
import { reactive, toRefs, watch, ref } from "vue";
import { useStore } from "vuex";
import HeaderNav from "../components/HeaderNav.vue";
import { ElMessage } from "element-plus";
import Artplayer from "../components/Artplayer.vue";
// import Artplayer from "artplayer";
import artplayerPluginDanmuku from "artplayer-plugin-danmuku";
export default {
  components: { HeaderNav, Artplayer },
  setup() {
    // 
    let titlebar = reactive({
      playback: 114514,
      danmu: 1919,
      times: "114-5-14  19:19:08",
    });
    // 
    let avatar = reactive({
      squareUrl:
        "https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png",
    });
    const store = useStore();
    const vname = ref(store.state.vname);
    const author = ref(store.state.author);
    //
    let danmuLiebiao = reactive({
      // ()
      danmukuLB: [],
      // 
      state: false,
      showList: 2, //1,
      // 
      changeBtn() {
        danmuLiebiao.showList = danmuLiebiao.state == false ? "1" : "2";
        danmuLiebiao.state = !danmuLiebiao.state;
        console.log("aaa");
      },
    });
    //
    let tuijian = reactive({
      // 
      tuijianData: [
        {
          name: "a",
          author: "aa",
          plays: 1,
          danmus: 1,
        },
        {
          name: "b",
          author: "bb",
          plays: 2,
          danmus: 2,
        },
        {
          name: "c",
          author: "cc",
          plays: 3,
          danmus: 3,
        },
        {
          name: "d",
          author: "dd",
          plays: 4,
          danmus: 4,
        },
        {
          name: "e",
          author: "ee",
          plays: 5,
          danmus: 5,
        },
        {
          name: "f",
          author: "ff",
          plays: 6,
          danmus: 6,
        },
        {
          name: "g",
          author: "gg",
          plays: 7,
          danmus: 7,
        },
      ],
    });
    // 
    let comment = reactive({
      circleUrl:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      pinglun: "",
      // 
      sendPinglun() {
        let addCom = {
          username: "tester",
          content: "",
          time: "xxxx-xx-xx 4:04:00",
          goods: "xxx",
        };
        addCom.content = comment.pinglun;
        comment.commentData.push(addCom);
      },

      commentData: [
        {
          username: "",
          content: "123456789",
          time: "20xx-12-11 20:08",
          goods: 514,
          answer: [
            {
              username: "",
              content: "ba",
              time: "20xx-10-01 10:48",
              goods: 114,
            },
            {
              username: "",
              content: "za",
              time: "20xx-10-01 10:48",
              goods: 10,
            },
          ],
        },
        {
          username: "",
          content: "qwer",
          time: "20xx-04-01 17:08",
          goods: 1919,
          answer: [
            {
              username: "",
              content: "gggc",
              time: "20xx-10-01 10:48",
              goods: 4,
            },
          ],
        },
        {
          username: "",
          content: "123456789",
          time: "20xx-05-21 10:38",
          goods: 810,
        },
      ],
      pinglunSuccess() {
        ElMessage({
          message: ".",
          type: "success",
        });
        comment.pinglun = "";
      },
      pinglunError() {
        ElMessage({
          message: ".",
          type: "error",
        });
      },
    });
    // 
    watch(
      () => [...comment.commentData],
      (now, old) => {
        if (now != old) {
          comment.pinglunSuccess();
        } else if (now == old) {
          comment.pinglunError();
        }
      }
    );

    return {
      ...toRefs(titlebar),
      ...toRefs(avatar),
      vname,
      author,
      // player,
      ...toRefs(danmuLiebiao),
      ...toRefs(tuijian),
      ...toRefs(comment),
    };
  },

  data() {
    return {
      currentTime: null,
      danmuCtx: {},
      art: {
        container: ".artplayer-app",
        url: require('@/assets/cx.mp4'),
        theme: "#23ade5",
        autoplay: true,
        fullscreen: true, //
        fullscreenWeb: true, //
        lang: "zh-cn",
        pip: true, //
        autoSize: true,
        autoMini: true,
        isLive: false, //
        muted: false, //
        setting: true,
        loop: false, //
        flip: true, //
        playbackRate: true, //
        aspectRatio: true, //
        // 
        highlight: [
          {
            time: 22,
            text: "logo",
          },
          {
            time: 43,
            text: "",
          },
          {
            time: 50,
            text: "",
          },
          {
            time: 89,
            text: "",
          },
          {
            time: 133,
            text: "",
          },
          {
            time: 172,
            text: "",
          },
        ],
        //hover
        thumbnails: {
          url: require('@/assets/cxthum.png'),
          number: 160, // 
          width: 160, // 
          column: 10,
        },
        // 
        danmukuList: this.danmukuList,
        // 
        plugins: [
          // 
          artplayerPluginDanmuku({
            danmuku: () => this.danmukuList,
            speed: 5, // [1 ~ 10]
            opacity: 1, // [0 ~ 1]
            fontSize: 25, // 
            color: "#FFFFFF", // 
            mode: 0, // 0-1-
            margin: [10, "25%"], // 
            antiOverlap: true, // 
            useWorker: true, //  web worker
            synchronousPlayback: false, // 
            filter: (danmu) => danmu.text.length < 50, //  true 
            lockTime: 5, // [1 ~ 60]
            maxLength: 100, // [0 ~ 500]
            minWidth: 200, // [0 ~ 500] 0 
            maxWidth: 400, // [0 ~ Infinity] 0  100% 
            theme: "dark", //  dark light
            beforeEmit: (danmu) => {
              let temp = danmu;
              this.danmuCtx = temp;
              this.sendDanmu();
              if (this.danmuCtx != {}) return true;
            }, //  true 
          }),
        ],
      },
      danmukuList: [
        // {
        //   text: "111", // 
        //   time: 1, // 
        //   color: "#fff", // 
        //   border: false, // 
        //   mode: 0, // : 0, 1
        //   date: "xx-xx xx:xx", //
        // },
      ],
      danmukuLength: 0,
      timer: 0,
    };
  },
  computed: {
    //  player(){
    //     return this.store1.state.art;
    //   }
  },
  watch: {
    // currentTime: function (newValue) {
    //   console.log("" + newValue);
    // },
  },

  beforeMount() {
    this.beginDanmu();
    // console.log("beforeMount", this.danmukuList);
    // console.log("beforeMount", this.danmukuLength);
  },
  mounted() {},
  methods: {
    // 
    randomColor() {
      var color = "#";
      for (let i = 0; i < 6; i++) {
        color += ((Math.random() * 16) | 0).toString(16);
      }
      return color;
    },
    // 
    beginDanmu() {
      let danmukuLeng = 0;
      let danmulist = [];
      let modee = 0;
      let timee = 0;
      let rgb = "";
      let num = "";
      let temp = {};
      for (let i = 0; i < 514; i++) {
        modee = Math.round(Math.random()); //0 to 1
        timee = Math.floor(Math.random() * 189) + 1; //1 to 189
        rgb = this.randomColor();
        num = i.toString();
        temp = {
          text: num,
          mode: modee,
          time: timee,
          color: rgb,
          border: false,
          date: "01-01 20:23",
        };
        danmulist.push(temp);
      }
      danmukuLeng = danmulist.length;
      this.danmukuList = danmulist;
      this.danmukuLength = danmukuLeng;
      this.art.currentTime = 4;
      console.log("", this.danmukuLength);
    },
    // 
    getCurrentTime() {
      this.$refs.ap.takeCurrentTime();
    },
    // 
    getCtime(ct) {
      this.currentTime = ct;
      // console.log(this.currentTime)
    },
    // 
    sendDanmu() {
      this.getCurrentTime();
      let date = this.getDate();
      setTimeout(() => {
        (this.danmuCtx.time = this.currentTime), (this.danmuCtx.date = date);
        this.danmukuList.push(this.danmuCtx)
        this.danmukuLength=this.danmukuList.length
        console.log(this.danmuCtx);
        console.log(this.danmukuList);
        console.log(this.danmukuLength);
      }, 1000);
    },
    // 
    getDate() {
      let date = new Date();
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate() + " ";
      let text = " ";
      let hh =
        date.getHours() < 10
          ? "0" + date.getHours() + ":"
          : date.getHours() + ":";
      let mm =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      let ss =
        date.getSeconds() < 10 ? "0" + date.getDate() : date.getSeconds();
      return M + D + text + hh + mm;
    },
  },
};
</script>

